import React , {useState} from 'react'
import SingleColor from './SingleColor'
import Values from 'values.js'
import './style.css'
function Main() {
 const [color , setColor]=useState('')
 const [error , setError] = useState(false)
 const [colorsList , setColorsList]=useState( new Values("#f20303").all(10))


 const supmitHandler = (e)=>{
  e.preventDefault();
  try {
   let colors = new Values(color).all(10)
   setColorsList(colors);
   console.log(colorsList)
  } catch (error) {
   setError(true)
   console.log(error)
  }
 }


 return <div className='container'>
  <section className='form-container'>
   <h1>color generator</h1>
   <form onSubmit={supmitHandler}>
    <div className="form-group">
      <input className='color-Selector' type="color" onChange={(e)=>setColor(e.target.value)}/>
     <input placeholder="#63309a" className={`${error? 'error' : 'default'}`} type="text" value={color} onChange={(e)=> setColor(e.target.value)} />
     <button type='submit' className='btn'>submit</button>
    </div>
   </form>
  </section>
  <section className='color-cotainer'>
  {
   colorsList.map((color , index)=>{
     return(
      <SingleColor key={index} index={index} {...color} hexColor={color.hex}/>
     )
   })
  }
  </section>
 </div>
}

export default Main
